// src/components/Contact.js

import React from "react";
import styled from "styled-components";
import { resume } from "../data/resumeData";
import { FaLinkedin, FaGithub, FaEnvelope } from "react-icons/fa";

const Section = styled.section`
  padding: 50px 20px;
  background: #f7f9fc; /* Light gray */
  text-align: center;
`;

const IconContainer = styled.div`
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1.5rem;
`;

const IconLink = styled.a`
  color: #2c3e50; /* Dark blue */
  font-size: 2rem;
  transition: color 0.3s;
  &:hover {
    color: #1abc9c; /* Teal */
  }
`;

const Contact = () => {
  const { aboutMe } = resume;
  return (
    <Section id="contact">
      <h2>Contact</h2>
      <IconContainer>
        <IconLink href={aboutMe.linkedin} target="_blank" rel="noopener noreferrer">
          <FaLinkedin />
        </IconLink>
        <IconLink href={aboutMe.github} target="_blank" rel="noopener noreferrer">
          <FaGithub />
        </IconLink>
        <IconLink href={`mailto:${aboutMe.email}`}>
          <FaEnvelope />
        </IconLink>
      </IconContainer>
    </Section>
  );
};

export default Contact;
