// src/components/AboutMe.js

import React from "react";
import styled from "styled-components";
import { resume } from "../data/resumeData";

const Section = styled.section`
  padding: 100px 20px 50px 20px;
  background: #ffffff; /* White */
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Container = styled.div`
  max-width: 1000px;
  display: flex;
  flex-direction: row;
  gap: 2rem;
  align-items: center;
  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const Photo = styled.img`
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
`;

const Bio = styled.div`
  flex: 1;
  color: #2c3e50; /* Dark blue */
  font-family: 'Roboto', sans-serif;
`;

const AboutMe = () => {
  const { aboutMe } = resume;
  return (
    <Section id="about">
      <h2>About Me</h2>
      <Container>
        <Photo src={aboutMe.photo} alt="Profile" />
        <Bio>
          <p>{aboutMe.bio}</p>
          <p>
            <strong>Contact:</strong> {aboutMe.phone} | {aboutMe.email}
          </p>
          <p>
            <strong>Links:</strong>{" "}
            <a href={aboutMe.linkedin} target="_blank" rel="noopener noreferrer">
              LinkedIn
            </a>{" "}
            |{" "}
            <a href={aboutMe.github} target="_blank" rel="noopener noreferrer">
              GitHub
            </a>
          </p>
        </Bio>
      </Container>
    </Section>
  );
};

export default AboutMe;
